import { Component, OnInit } from '@angular/core';
import { getLocaleDateFormat } from '@angular/common';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'maddy';

  timer: boolean;
  loaded: boolean;

  hours: any;
  minutes: any;
  seconds: any;
  height: any;

  Data: any;

  constructor(private http:HttpClient){
    this.timer = false;
  }

  ngOnInit(){
    this.timer = false;

    let header = document.getElementsByClassName("header")[0];
    this.height = document.documentElement.clientHeight - header.clientHeight - 40;

    setInterval(() => {
      let bDay = new Date();
      bDay.setDate(4);
      bDay.setMonth(3);
      bDay.setFullYear(2020);
      bDay.setHours(0);
      bDay.setMinutes(0);
      bDay.setSeconds(0);
      bDay.setMilliseconds(0);

      let now = new Date();
      let dif = bDay.getTime() - now.getTime();
      
      if(dif > 0) this.timer = false;
      else this.timer = true;
      this.hours = Math.abs(23 - now.getHours());
      this.minutes = Math.abs(60 - now.getMinutes());
      this.seconds = Math.abs(60 - now.getSeconds());
    },1000);

    this.getData();
    setTimeout(() => {
      this.loaded = true;
    },100)
  }

  getData(){
    this.http.get("assets/data.json").subscribe((data) => {
      this.Data = data;
    })
  }

}
